
'use strict';

/**
 *  
 * @version 1.1.0
 * @author xfl <541151284@qq.com>
 */
class  Downloader{
    Downloader = Downloader;
    
	/**
	 * 
	 * @public
	 * @param {String} url url
	 * @param {String} [name] 
	 * @returns {Promise}  [err, res]
	 * @example
	 * 
	    let promise = downloader.load(url, imgName);  
		promise.then(([err, res])=>{    
			console.log(err, res);  // err  res null  
		});
	 */
    async load(url, name){
		url = url.trim();
		
		name = this.getName(url, name); //
		
		let err, res;
		if( !this.isRemoteUrl(url) ){ 

				[err, res] = [null, {tempFilePath: url}];

		}else{
			//webdownloadFile 
			[err, res] = await uni.downloadFile({url: url});
			if(err){ //h5
				return [err, null];
			}else if(res.statusCode !== 200){ //404
				res.errMsg = "downloadFile:fail";
				return [res, null];
			}
		}
        













        

        [err, res] =  await  uni.saveImageToPhotosAlbum({filePath: res.tempFilePath});
        if( err ){
            return [err, null];
        }

		
		return [null, res];
    }
	
	/**
	 * 
	 * @public
	 * @param {String} url url, url
	 * @param {String} [name]   .
	 * @param {String} [ext]  
	 * @returns {String}  .
	 * @example
	 * 
	 * 1. getName('aaa/bbbb.txt');
	 * //=> bbbb.txt
	 * 
	 * 2. getName('aaa/bbbb.txt', 'uuuuu');
	 * //=> uuuuu.txt
	 * 
	 * 3. getName('aaa/bbbb.txt', 'uuuuu', '.js');
	 * //=> uuuuu.js
	 * 
	 * 4. getName('aaa/bbbb.txt', 'uuuuu.kkk', '.js');
	 * //=> uuuuu.js
	 * 
	 * 5. getName('aaa/bbbb.txt', 'uuuuu.kkk');
	 * //=> uuuuu.kkk
	*/
	getName(url, name, ext){
		let arr = url.match(/(([\w\d_\-]+)(\.[\w\d_]+))$/); //
		
		// url 
		let defaultName = '__default', defaultExt = '.png';
		if(arr){
			defaultName = arr[1] || defaultName;
			defaultExt = arr[3] || defaultExt;
		}
		
		// name 
		arr = name.match(/\.[\w\d_]+$/);
		if(arr){
			ext = ext || arr[0];
			name = name.replace(/\.[\w\d_]+$/, '');  //name
		}
		
		ext = ext || defaultExt;
		name = name || defaultName;
		
		return name + ext;
	}
	
	
	/**
	 * 
	 * @public
	 * @param {String} url url
	 * @returns {Boolean} 
	*/
	isRemoteUrl(url){
		return !!url.match(/^https?:\/\//i);
	}
	
	 /**
	  * weba
	  * 
	  * (html)
	  * @public
	  * @param {String} url 
	  * @param {String} [name=''] 
	  */
    webDownloadImg(url, name = ''){  //
        const body = document.getElementsByTagName('body')[0];
        const aEle = document.createElement('a');
		aEle.setAttribute('download', name);
		aEle.style.display = 'none';
        aEle.href =  url;
        aEle.target = '_blank';
        
        body.appendChild(aEle);
        aEle.dispatchEvent( newMouseEvent('click') );  // click
        document.body.removeChild(aEle);
    }
	
	/**
	 *   
	 * @static
	 * @public
	 */
	static getInstance(){
		if(!this.__hasInstance){
			this.__hasInstance = new this();
		}
		return this.__hasInstance;
	}
}

export default Downloader.getInstance();
