const share = {
    wxContentImage: function(params, callback) {
        // 
        uni.share({
            provider: "weixin",
            scene: params.scene || "WXSceneSession",
            type: 0,
            href: params.href || "",
            title: params.title || "title",
            summary: params.summary || "title",
            imageUrl: params.imageUrl ||
                "https://t12.baidu.com/it/u=224094710,3804416768&fm=76",
            success: function(res) {
                callback && callback({
                    code: res.errMsg == "share:ok" ? 1 : 0,
                    data: res
                })
            },
            fail: function(err) {
                callback && callback({
                    code: 0,
                    data: err
                })
            }
        });
    },
    wxContent: function(params, callback) {
        // 
        uni.share({
            provider: "weixin",
            scene: params.scene || "WXSceneSession",
            type: 1,
            summary: params.summary || "~",
            success: function(res) {
                callback && callback({
                    code: res.errMsg == "share:ok" ? 1 : 0,
                    data: res
                })
            },
            fail: function(err) {
                callback && callback({
                    code: 0,
                    data: err
                })
            }
        });
    },
    wxImage: function(params, callback) {
        // 
        uni.share({
            provider: "weixin",
            scene: params.scene || "WXSceneSession",
            type: 2,
            imageUrl: params.imageUrl ||
                "",
            success: function(res) {
                callback && callback({
                    code: res.errMsg == "share:ok" ? 1 : 0,
                    data: res
                })
            },
            fail: function(err) {
                callback && callback({
                    code: 0,
                    data: err
                })
            }
        });
    },
    wxMiniProgram: function(params, callback) {
        uni.share({
            provider: 'weixin',
            type: 5,
            imageUrl: params.imageUrl ||
                "",
            title: params.title || "",
            miniProgram: {
                id: params.wxAppid || "APPID",
                path: params.path || '/pages/index/index',
                type: params.path || 0, //  0- 1- 2- 0 
                webUrl: params.path || 'http://uniapp.dcloud.io'
            },
            success: ret => {
                console.log(JSON.stringify(ret));
            }
        });
    }
}

export default share
